import React from "react";

import Person from "./Person";

export default function Persons({ persons, deletePerson }) {
  return (
    <div>
      {persons.map((p) => (
        <div>
          <Person key={p.id} person={p} />
          <button onClick={() => deletePerson(p)}>Delete</button>
        </div>
      ))}
    </div>
  );
}
