import React, { useEffect, useState } from "react";
import "./styles.css";

import Filter from "./components/Filter";
import PersonForm from "./components/PersonForm";
import Persons from "./components/Persons";
import Notification from "./components/Notification";

import personServices from './services/person';

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [filterName, setFilterName] = useState("");
  const [notifSetting, setNotifSeting] = useState({ type: "", message: "", isActive: false });


  // Get data from json server
  useEffect(() => {
    personServices.getAll()
      .then(allPerson => {
        setPersons(allPerson)
      })
  }, [])

  // Prevent form from refreshing the page on Submit
  // Update the persons array with the new name
  const addPerson = (event) => {
    event.preventDefault();

    let found = checkIfExists();
    if (found) {

      const foundObject = {
        name: found.name,
        phone: newNumber
      }

      personServices.updateNumber(found.id, foundObject)
        .then(returnPerson => {
          console.log("Return Person", returnPerson)
          setPersons(persons.map(person => person.id !== found.id ? person : returnPerson));
        })

    } else {
      const personObject = {
        name: newName,
        phone: newNumber
      };

      personServices.create(personObject)
        .then(returnPerson => {
          console.log("Return Person", returnPerson)
          setPersons(persons.concat(returnPerson));
        })
    }

    activateNotification("success", `Added ${newName}`);

    setNewName("");
    setNewNumber("");
  };

  // Track the Name input on Change
  const handlePersonChange = (event) => {
    setNewName(event.target.value);
  };

  const handleNumberChange = (event) => {
    // console.log(event.target.value);
    setNewNumber(event.target.value);
  };

  // Check if name already exists
  // should be ran before inserting to persons array
  const checkIfExists = () => {
    const found = persons.find((person) => person.name === newName);
    if (found) {
      return found;
    }
    return false;
  };

  // filter people
  const filterPeople = (event) => {
    event.preventDefault();
    // Search for the name in the array
    const results = persons.filter((word) =>
      word.name.toUpperCase().includes(filterName.toUpperCase())
    );
    if (results.length > 0) {
      setPersons(results);
    } else {
      alert(`${filterName} was not found.`);
      setFilterName("");
    }
  };

  const handleFilterNameChange = (event) => {
    setFilterName(event.target.value);
  };


  // Delete a person from database
  const deletePerson = (p) => {
    console.log(p.id);
    if (window.confirm(`Delete ${p.name}`)) {
      personServices
        .deletePerson(p.id)
        .then(result => {
          let newArray = persons.filter(person => person.id !== p.id);
          setPersons(newArray)
        })
        .catch(error => {
          activateNotification("error", `${p.name} has already been removed from server!`);
        })

      activateNotification("warning", `${p.name} was deleted from server`);
    }
  }


  // notification
  const activateNotification = (type, message) => {
    const notifObject = {
      type: type,
      message: message,
      isActive: true
    }
    setNotifSeting(notifObject);

    setTimeout(() => {
      setNotifSeting({ type: "", message: "", isActive: false })
    }, 2000)
  }

  return (
    <div className="App">
      <h1>Phonebook</h1>
      <Notification notificationSettings={notifSetting} />
      <Filter
        filterPeople={filterPeople}
        filterName={filterName}
        handleFilterNameChange={handleFilterNameChange}
      />
      <h2>Add New Number</h2>
      <PersonForm
        addPerson={addPerson}
        newName={newName}
        handlePersonChange={handlePersonChange}
        newNumber={newNumber}
        handleNumberChange={handleNumberChange}
      />
      <h2>Numbers</h2>
      <Persons persons={persons} deletePerson={deletePerson} />
    </div>
  );
};

export default App;
